@use 'base/variables' as v;
@use 'base/mixins' as m;

.contedor-carrito {
    max-width: 100%;
    overflow-x: auto;
}
.contenedor-contenido {
    margin-bottom: 2rem;
}

table.table-carrito {
    margin-bottom: 10rem;
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: v.$negro;

        th {
            color: v.$blanco;
            padding: 2rem;
        }

        td {
            padding: 2rem;
            text-align: center;
        }
    }
    .td-button{
        margin: 0;
        padding: 0;
       
    }

}
.contenedor-tabla {
    @include m.tablet {
        overflow-x: inherit;
        width: 100%;
    }
    max-width: 100%;
    overflow-x: auto;
}
table.tabla {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead {
        background-color: v.$negro;

        th {
            padding: 2rem;
            color: v.$blanco;
        }
        td {
            padding: 1rem;
            text-align: center;
        }


    }
    .td-botones-tabla{
        max-width: .8rem;
        a{
            padding: 1rem 2rem;
        }
        form{
            input {
                padding: 1rem 1.8rem;
                width:  14.5rem;
            }
        }
    }

    .imagen-tabla {
        width: 4rem;
    }
}

@include m.tablet {
    table.tabla {
        margin-top: 4rem;
        width: 100%;
        border-spacing: 0;
        thead {
            background-color: v.$negro;

            th {
                padding: 2rem;
                color: v.$blanco;
            }
            td {
                padding: 1rem;
                text-align: center;
            }


        }

        .imagen-tabla {
            width: 10rem;
        }
        .td-botones-tabla{

            margin-top: .8rem;
            form{
                margin: 1rem 0 0.6rem 0;
                input {
                    width: 14.5rem;
                }
            }
        }
    }

}

.contenedor-pagar{
    margin-bottom: 15rem;
}