@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta{
    display: block;
    color: v.$blanco;
    padding: calc(v.$separacion / 4);
    font-weight: 700;
    border: none;
    text-align: center;
    margin-bottom: calc(v.$separacion / 4);
    text-transform: uppercase;
}

.error{
    background-color: v.$rojo;
}
.exito{
    background-color: v.$verde;
}