@use 'base/variables' as v;
@use 'base/mixins' as m;



.contenedor-detalles {

    @include m.tablet {
        @include m.grid(2, 4rem);
    }
    img{
        margin-top: 10rem;
    }

    h2 {
        margin: 5rem 0;
    }

    h3 {
        margin: 0;
        text-align: left;
    }

    input {
        margin: 2rem 0;
        width: 100%;
    }

    .descuento {
        color: v.$verde;
        font-size: 3rem;
        margin: 0;
    }

    .precio {
        p {
            font-weight: 900;
            font-size: 3rem;
        }

        display: flex;
        justify-content: space-between;
    }

}

.cantidad-detalles{
    margin-bottom: 12rem;
    input{
        margin: 0;
    }
    label{
        font-size: 2rem;
        font-weight: v.$bold;
        color: v.$negro;
    }
    @include m.tablet {
        @include m.grid(2, 2px);

    }
}