@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: black;
    padding: 2rem 0;
}

.footer-content{
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    
    p, a{
        color: white;
        font-size: 1.5rem;
    }
   
    nav {
       a {
         font-size: 2.5rem;
         i {
            margin-top: 1.2rem;
         }
         
       } 
    }
    
    
}

.footer-fixed {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}