.slider-contenedor .slider-img1 {
  background-image: url(../img/fondo0.jpg);
}
.slider-contenedor .slider-img2 {
  background-image: url(../img/fondo1.webp);
}
.slider-contenedor .slider-img3 {
  background-image: url(../img/fondo2.jpg);
}
.slider-contenedor .imgs {
  width: 100%;
  margin: 0;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  min-height: 40rem;
}
