.slider-contenedor {
  display: grid;
  grid-template-columns: 50px 1fr 50px;
}
.slider-contenedor .slider-body {
  grid-column: 2/3;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 1fr max-content;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 1.5s;
}
.slider-contenedor .slider-body-show {
  opacity: 1;
}
.slider-contenedor .slider-arrow {
  cursor: pointer;
  width: 90%;
