@use 'base/variables' as v;
@use 'base/mixins' as m;

.incrementar{
    input[type="number"] {
      width: 100%;
      -webkit-appearance: textfield;
      appearance: textfield;
      border: 1px solid ;
      background-color: v.$negro;
      color: v.$blanco;
      font-size: 14px;
      text-align: center;
      }

      input[type="number"]:focus {
      outline: none;
      border: 1px solid v.$blanco;
      background-color: v.$rojo;
      color: v.$blanco;
      }
}

