@use "base/variables" as v;
@use "base/mixins" as m;

body {
    background-color: v.$blancos;
}

.header {
    background-color: v.$negro;
    
    
    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .2rem v.$negro;
        }
    }

    h1 {
        color: v.$negro;
        margin: 0;
    }

    .contenido-header {
        text-align: center;
        display: flex;
        padding: 3rem;
        align-items: center;

        .logo {
            width: 25rem;
        }

        @include m.tablet {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        display: block;

    }
}

.navegacion-principal {
    text-transform: uppercase;
    a {
        font-size: 2rem;
        margin: .8rem;
        color: v.$blancos;
        font-weight: v.$bold;
        &:hover {
            color: v.$rojo;
        }
    }

    img {
        width: 3.5rem;
        filter: invert(75%);
    }

   

    .numero_carrito {
        font-size: 1.5rem;
        color: v.$blanco;
        font-weight: v.$bold;
    }
}



.lupa {
    display: flex;
    @include m.tablet {
        border: 1px solid v.$blanco;
    }

    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration {
        display: none;
    }
   

    img {
        @include m.tablet {
            width: 3rem;
        }

        filter: invert(75%);
    }


    button {
        @include m.tablet {
            background-color: v.$negro;
            display: block;
        }
        display: none;
    }

    input {
        border: 1px solid black;
        border-radius: .5rem;
        padding: 1rem 8rem 1rem 1rem;
        @include m.tablet {
            padding: 1rem 15rem 1rem 1rem;
        }
    }
}