@use 'base/variables' as v;
@use 'base/mixins' as m;

.section-vendidos{
    background-color: v.$grisClaro;
    .mas-vendidos{
        h2{
            color: v.$negro;
            margin: 4rem;
        }
    }
}



.contenedor-vendidos {
    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        gap: 2rem;
    }
    picture{
        img{
            width: 100%;
            height: 17rem;
            overflow: hidden;
        }
    }
}

.mas_vendidos{
    padding-top: 1rem;
    padding-bottom: 8rem;
    background-color: v.$blanco;
    border-radius: 2%;
    margin: 4rem 0;
    h3,p,a{
        color: v.$negro;
    }
}

.precio{
    text-align: center;
    font-weight: v.$bold;
}


