@use 'variables' as v;


html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
button {
    border: none;
    background: none;
    box-shadow: none;
}
form label {
    border: none;
    background: none;
    box-shadow: none;
    font-size: inherit;
    color: inherit;
}
  
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$negro;
    color: v.$blancos;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.m-0{
 margin: 0;
}
.w-0{
 width: 0;
}
.w-auto{
 width: auto;
}
.w-full{
 width: 100%;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.flex{
    display: flex;    
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
