@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado {
    h2 {
        color: v.$negro;
        margin: 4rem;
    }
}
.contenedor-listado {
    @include m.grid(1, 4rem);  
    @include m.tablet {
        @include m.grid(4, 2rem);  
    }
    margin-bottom: 2rem;
}

.listado-fondo {
    display: grid;
    grid-template-rows: 3fr 1fr;
    background-color: v.$negro;
    height: 45rem;
    img {
       width: auto;
    }
    h3,
    p {
        margin: 0;
        color: v.$blanco;
    }
   form{
        display: inline-grid;
        align-items: end;
        margin: 0;
   }
    .incrementar{
        margin-top: 1rem;
    }
    .informacion {
        @include m.grid(2, 1rem);
        margin: .5rem;
        .precio-oferta{
                color: v.$verde; 
        }
        .descuento{
            font-weight: v.$bold;
            color: v.$verde;
        }
        .nombre{
            font-size: 1.5rem;
        }
    }
    

}


.cantidad-categorias {
    label{
        font-size: 2rem;
        font-weight: v.$bold;
    }
    @include m.tablet {
        @include m.grid(2, 2px);

    }
}

.botones-compra{
    @include m.grid(2, 2px);
    a{
        i{
            margin-left: .5rem;
        }
    }
    a,
    button{
        margin: 0;
    }
}