@use 'base/variables' as v;
@use 'base/mixins' as m;

.swal2-popup  {
    width: 50rem;
    height: 35rem;
    h1 {
        font-size: 4rem;
    }
    .swal2-html-container {
        font-size: 2rem;
    }
    .swal2-confirm {
        font-size: 2rem;
    }
    .swal2-success-ring{
        width: 8rem;
    }
}

.pagar-botones {
    display: flex;
    justify-content: center;
    form {
        button {
            padding: 1rem 5rem;
        }
    }
    a {
        padding: 1rem 5rem;
    }
}