@use 'base/variables' as v;
@use 'base/mixins' as m;


.ventas {
    
    li {
        border-top: 1px v.$negro solid;

        p {
            font-weight: v.$bold;
        }

        span {
            font-weight: v.$regular;
        }

    }
    
    .productos{
        display: flex;
        
        justify-content: space-between;
        background-color: v.$grisClaro;
        padding: 1rem;
        
        p{
            margin: 0;
            font-weight: v.$regular;
            text-align: center;
        }
        
    }
    .total{
        color: v.$verde;
        font-weight: v.$bold;
    }

}

.imagen-small {
    width: 20rem;
}