@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-color {
    background-color: v.$negro;
}
.categoria-img {
    p {
        margin-top: 1rem;
    }
}
.contenedor-categorias {
    display: flex;
    justify-content: center;
    margin-right: 49rem;
    gap: 2rem;
    img{
        width: 3.5rem;
        height: 3.2rem;
        filter: invert(75%);
        margin-top: .5rem;
   
        &:hover {
            cursor: pointer;
        }
    }
    .titulo-descuento, p{
        color: v.$blanco;
        margin-top: 1rem;
        text-align: left;
        font-size: 1.7rem;
        font-weight: v.$bold;

        &:hover {
            cursor: pointer;
            color: v.$rojo;
        }
    }
}

.display {
    position: absolute;
    z-index: 100;
    background-color: v.$blancos;
    a {
        transition-property: background-color;
        transition-duration: .3s;
        display: block;
        font-size: 2.3rem;
        color: v.$negro;
        padding: 1.5rem 4rem;
        &:hover {
            background-color: darken(v.$rojo, 10%);

        }
    }
}


